@charset "UTF-8";
@font-face {
  font-family: "opensans-bold";
  src: url("../fonts/opensans-bold.woff") format("woff"), url("../fonts/opensans-bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "opensans-semibold";
  src: url("../fonts/opensans-semibold.woff") format("woff"), url("../fonts/opensans-semibold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "opensans-regular";
  src: url("../fonts/opensans-regular.woff") format("woff"), url("../fonts/opensans-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "opensans-light";
  src: url("../fonts/opensans-light.woff") format("woff"), url("../fonts/opensans-light.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
  line-height: 32px;
  font-family: "opensans-light", "Arial", sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: relative;
  margin: 0;
  background: #ffffff;
}
body::-webkit-scrollbar {
  width: 0;
}

@media (max-width: 1023px) {
  body.menu-open {
    overflow: hidden;
  }
}

img {
  display: block;
  object-fit: cover;
}

.button-burger {
  display: none;
  justify-content: center;
  align-items: center;
  width: 49px;
  height: 49px;
  padding: 0;
  cursor: pointer;
  border: none;
  background-color: #0ad9c6;
  transition-duration: 0.5s;
}
.button-burger:hover, .button-burger:focus {
  filter: brightness(115%);
}

.button-burger__line {
  position: relative;
}
.button-burger__line, .button-burger__line::before, .button-burger__line::after {
  width: 23px;
  height: 1px;
  background-color: #000000;
}
.button-burger__line::before, .button-burger__line::after {
  content: "";
  top: 0;
  left: 0;
  position: absolute;
  transition-duration: 0.3s;
}
.button-burger__line::before {
  transform: translateY(-5px);
}
.button-burger__line::after {
  transform: translateY(5px);
}

.button-burger.open {
  background-color: transparent;
}
.button-burger.open .button-burger__line {
  height: 0;
  transition-duration: 0.3s;
}
.button-burger.open .button-burger__line::before,
.button-burger.open .button-burger__line::after {
  background-color: rgba(0, 0, 0, 0.2);
  width: 30px;
  transition-duration: 0.3s;
}
.button-burger.open .button-burger__line::before {
  transform: rotate(45deg);
}
.button-burger.open .button-burger__line::after {
  transform: rotate(-45deg);
}
.button-burger.open:hover, .button-burger.open:focus {
  filter: brightness(100%);
}
.button-burger.open:hover .button-burger__line::before,
.button-burger.open:hover .button-burger__line::after, .button-burger.open:focus .button-burger__line::before,
.button-burger.open:focus .button-burger__line::after {
  background-color: rgba(0, 0, 0, 0.7);
}
.button-burger.open:hover .button-burger__line, .button-burger.open:focus .button-burger__line {
  transform: rotate(90deg);
  transform-origin: center center;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "opensans-semibold", "Arial", sans-serif;
  font-size: 1rem;
  line-height: 1.3rem;
  letter-spacing: -0.6px;
  padding: 15px;
  color: #000000;
  background-color: #0ad9c6;
  border: none;
  border-radius: 6px;
  text-decoration: none;
  cursor: pointer;
}
@media (max-width: 766px) {
  .button {
    padding: 16px 10px 14px 10px;
  }
}
.button:hover, .button:focus {
  filter: brightness(115%);
}

.button-play {
  width: 65px;
  height: 65px;
  position: relative;
  border-radius: 50%;
  background-color: #0ad9c6;
  border: none;
  cursor: pointer;
  transition-duration: 0.5s;
}
@media (min-width: 766px) and (max-width: 1023px) {
  .button-play {
    width: 90px;
    height: 90px;
  }
}
.button-play::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  top: 50%;
  left: calc(50% + 2px);
  transform: translate(-50%, -50%);
  border: 13px solid transparent;
  border-left-color: #070707;
  border-top-width: 9px;
  border-bottom-width: 9px;
  border-right: none;
}
@media (min-width: 766px) and (max-width: 1023px) {
  .button-play::before {
    border-left-width: 18px;
    border-top-width: 13px;
    border-bottom-width: 13px;
  }
}
.button-play:hover, .button-play:focus {
  filter: brightness(115%);
  transform: translate(-50%, -50%) scale(1.2);
}

