.indexed-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  counter-reset: steps;
}

.indexed-list__item {
  counter-increment: steps;
  display: grid;
  max-width: 262px;
  margin: 0 20px 42px 20px;
  text-align: center;
  font-family: "opensans-regular", "Arial", sans-serif;
  font-size: 1.38rem;
  line-height: 2.13rem;
  letter-spacing: -0.6px;
}
@media (max-width: 766px) {
  .indexed-list__item {
    font-size: 1.13rem;
    margin: 0 16px 42px 16px;
  }
}
.indexed-list__item::before {
  font-family: "opensans-bold", "Arial", sans-serif;
  font-size: 1.75rem;
  line-height: 2.38rem;
  content: "0" counter(steps);
  color: #0ad9c6;
  margin-bottom: 12px;
}
@media (max-width: 1023px) {
  .indexed-list__item::before {
    margin-bottom: 16px;
  }
}
@media (max-width: 766px) {
  .indexed-list__item::before {
    margin-bottom: 10px;
  }
}

.form {
  display: flex;
  flex-direction: column;
  position: relative;
}
.form::before, .form::after {
  position: absolute;
  content: "";
  visibility: hidden;
  width: 0;
  height: 0;
  transition: width, height 0.3s ease-in-out;
}

.form.success::before, .form.success::after {
  visibility: visible;
}
.form.success::before {
  width: 100px;
  height: 100px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  border: 8px solid rgba(10, 217, 198, 0.5);
  background-color: rgba(255, 255, 255, 0.7);
}
.form.success::after {
  width: 30px;
  height: 40px;
  top: calc(50% - 33px);
  left: calc(50% - 18px);
  transform: rotate(45deg);
  border-bottom: 8px solid rgba(10, 217, 198, 0.5);
  border-right: 8px solid rgba(10, 217, 198, 0.5);
}

.form.error::before, .form.error::after {
  visibility: visible;
}
.form.error::before {
  content: "Ошибка";
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
  height: auto;
  padding: 20px 30px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 3px solid #f13030;
  border-radius: 6px;
  background-color: rgba(0, 0, 0, 0.5);
  font-family: "opensans-bold", "Arial", sans-serif;
  font-size: 1.6rem;
  line-height: 2rem;
  color: #ffffff;
}
.form.error::after {
  display: none;
}

.form__description {
  margin: 0;
  margin-bottom: 20px;
  font-family: "opensans-semibold", "Arial", sans-serif;
  line-height: 1.75rem;
  color: #ffffff;
