    letter-spacing: -0.4px;
  }
}
.form__field input:hover, .form__field input:focus {
  outline: 2px solid #0ad9c6;
}
.form__field input:hover::placeholder, .form__field input:focus::placeholder {
  color: rgba(28, 28, 28, 0.8);
}

.form__field.error {
  outline: 2px solid #f13030;
}

.card {
  position: relative;
  height: max-content;
  min-width: 270px;
  width: 100%;
  max-width: 342px;
  margin: 0 25px 80px 25px;
}
@media (max-width: 1023px) {
  .card {
    margin-bottom: 50px;
  }
}
@media (max-width: 766px) {
  .card {
    min-width: 200px;
    margin-bottom: 40px;
  }
}
.card::before, .card::after {
  content: "";
  position: absolute;
  transition-duration: 0.5s;
}
.card:hover::before, .card:hover::after {
  transform: scale(1.2);
}
.card figure {
  display: block;
  min-width: 200px;
  width: 100%;
  max-width: 342px;
  margin: 0;
}

.card:nth-child(3n+1) {
  z-index: 0;
  margin-top: -30px;
}
@media (max-width: 1023px) {
  .card:nth-child(3n+1) {
    margin-top: 0;
  }
}
.card:nth-child(3n+1)::before {
  top: 158px;
  left: -56px;
  width: 126px;
  height: 126px;
  border: 27px solid #0ad9c6;
  border-radius: 50%;
  z-index: -1;
}
@media (max-width: 1023px) {
  .card:nth-child(3n+1)::before {
    width: 90px;
    height: 90px;
    top: 203px;
    left: -37px;
    border-width: 18px;
  }
}
@media (max-width: 766px) {
  .card:nth-child(3n+1)::before {
    width: 86px;
    height: 86px;
    top: 205px;
    left: -24px;
  }
}

.card:nth-child(3n+2) {
  justify-self: center;
}
.card:nth-child(3n+2)::before, .card:nth-child(3n+2)::after {
  width: 31px;
  height: 31px;
  border: 8px solid #0ad9c6;
}
@media (max-width: 1023px) {
  .card:nth-child(3n+2)::before, .card:nth-child(3n+2)::after {
    display: none;
  }
}
.card:nth-child(3n+2)::before {
  top: -16px;
  left: -18px;
}
.card:nth-child(3n+2)::after {
  bottom: 31px;
  right: -22px;
}
.card:nth-child(3n+2):hover::before {
  transform: scale(1.2) rotate(135deg);
}
.card:nth-child(3n+2):hover::after {
  transform: scale(1.2) rotate(-135deg);
}

.card:nth-child(3n) {
  margin-top: -40px;
  justify-self: end;
}
@media (max-width: 1023px) {
  .card:nth-child(3n) {
    margin-top: 0;
  }
}
.card:nth-child(3n)::before {
  top: -30px;
  right: -36px;
  width: 61px;
  height: 61px;
  border: 17px solid #0ad9c6;
  border-radius: 50%;
}
@media (max-width: 1023px) {
  .card:nth-child(3n)::before {
    top: -21px;
    right: -25px;
    width: 42px;
    height: 42px;
    border-width: 13px;
  }
}
@media (max-width: 766px) {
  .card:nth-child(3n)::before {
    width: 39px;
    height: 39px;
    right: -12px;
  }
}

